html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  background-color: #DEE8EC;
  font-family: "Inconsolata", sans-serif, arial;
  font-size: 1.6rem;
}

a {
  cursor: pointer;
  text-decoration: none;
}

img {
  display: block;
  height: auto;
  max-width: 100%;
  width: 100%;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

/** Media Queries **/
h1, h2, h3 {
  margin: 0 0 2.5rem 0;
  color: #023047;
  text-align: center;
  text-transform: uppercase;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #023047;
  font-size: 2rem;
}

.btn-orange {
  background-color: #023047;
  border: 1px solid #FFB703;
  border-radius: 0.5rem;
  color: #FFB703;
  font-weight: 700;
  padding: 1rem 2rem;
}
.btn-orange:hover {
  background-color: #FFB703;
  color: #023047 !important;
}

.card {
  background-color: #F3FCFF;
  border-radius: 0.5rem;
  display: flex;
  flex-direction: column;
  padding: 1.6666666667rem;
  -webkit-box-shadow: 1px 2px 9px -2px rgba(2, 48, 71, 0.42);
  -moz-box-shadow: 1px 2px 9px -2px rgba(2, 48, 71, 0.42);
  box-shadow: 1px 2px 9px -2px rgba(2, 48, 71, 0.42);
  width: 100%;
}
@media (min-width: 768px) {
  .card {
    width: 48rem;
  }
}
.card .card-header {
  padding: 0;
}

.card-body {
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
}
@media (min-width: 768px) {
  .card-body {
    flex-direction: row;
  }
}
.card-body .card-image {
  flex: 1;
}
.card-body .card-text {
  flex: 2;
}
.card-body .card-image img,
.card-body .card-image picture {
  width: 100%;
  aspect-ratio: 3/2;
  object-fit: contain;
}
.card-body .card-text {
  border-top: 1px solid #DEE8EC;
  padding-left: 1.6666666667rem;
}
@media (min-width: 768px) {
  .card-body .card-text {
    border: none;
    border-left: 1px solid #DEE8EC;
  }
}
.card-body .card-text p {
  font-size: 1.6rem;
}

.header {
  background-color: #DEE8EC;
  height: 100vh;
  max-width: 200rem;
  margin: 0 auto;
}

.top-menu {
  display: flex;
  padding: 2.5rem;
  justify-content: space-between;
}

.menu {
  display: none;
}
@media (min-width: 1024px) {
  .menu {
    display: block;
  }
}
.menu a {
  color: #023047;
  padding: 1rem;
  font-size: 1.8rem;
  font-weight: 700;
  transition: color 0.3s ease-in-out;
}
.menu a:hover {
  color: #FFB703;
  border-bottom: 1px solid #FFB703;
}

.container-title {
  margin-top: 2.5rem;
  padding: 2rem;
}
.container-title h1 {
  color: #023047;
  letter-spacing: 0.5rem;
  font-size: 3rem;
  font-weight: 900;
  text-align: left;
}
@media (min-width: 768px) {
  .container-title h1 {
    font-size: 5rem;
  }
}
@media (min-width: 1024px) {
  .container-title h1 {
    font-size: 5rem;
  }
}

.text-shadow {
  color: #DEE8EC;
  display: block;
  font-size: 6rem;
  padding-top: 1rem;
  letter-spacing: 1rem;
  text-shadow: 0.2rem 0.2rem 1.2rem #023047;
}
@media (min-width: 768px) {
  .text-shadow {
    font-size: 13rem;
  }
}
@media (min-width: 1024px) {
  .text-shadow {
    font-size: 20rem;
    margin: 0;
  }
}

.container-social-networks {
  display: flex;
  gap: 5rem;
  margin-top: 3rem;
}
.container-social-networks a {
  width: 3rem;
  filter: drop-shadow(0.1rem 0.1rem 0.2rem #023047);
  transition: filter 0.2s ease-in-out;
}
@media (min-width: 768px) {
  .container-social-networks a {
    width: 6rem;
  }
}
.container-social-networks a:hover {
  filter: drop-shadow(0.1rem 0.1rem 0.2rem #8ECAE6);
}

.about-me {
  display: flex;
  flex-direction: column;
}

.container-resume {
  padding: 2rem;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.container-resume p {
  color: #023047;
  line-height: 1.5;
  letter-spacing: 0.1rem;
}
@media (min-width: 1024px) {
  .container-resume p {
    width: 50%;
  }
}
.container-resume .about-me {
  text-align: right;
}
@media (min-width: 1024px) {
  .container-resume .about-me {
    text-align: left;
  }
}
.container-resume .link-download {
  border-bottom: 2px solid #219EBC;
  color: #219EBC;
  font-size: 2rem;
  font-weight: 900;
  margin-right: 3rem;
  padding-bottom: 0.5rem;
  transition: all 0.2s ease-in-out;
}
.container-resume .link-download:hover {
  color: #FFB703;
  border-bottom: 2px solid #FFB703;
}

.profile-name {
  font-size: 3rem;
  font-weight: 700;
  text-align: right;
  margin: 0;
}
@media (min-width: 1024px) {
  .profile-name {
    font-size: 5rem;
  }
}
.profile-name .subtitle {
  display: block;
  font-size: 2.5rem;
  font-weight: 400;
}

.skills__container {
  display: flex;
  flex-wrap: wrap;
  gap: 5rem;
  justify-content: center;
  margin-top: 7.5rem;
}
@media (min-width: 1024px) {
  .skills__container {
    justify-content: space-between;
  }
}